import React from 'react';
import { Switch, Route, Link } from 'react-router-dom'

import styles from './style.module.scss';

import Input from 'components/input';
import Subscribe from 'components/subscribe';
import Card from 'components/card';
import { isBoolean } from 'util';

class TaxCalc extends React.Component {

	constructor(props){
		super(props)
		this.version = 1;
		if (localStorage.getItem('version')==null || localStorage.getItem('version')==null || parseInt(localStorage.getItem('version'))<1){
			localStorage.setItem('data', '')
			localStorage.setItem('version', '1');
		}
		this.defaultData = {
			'salary': { type: 'int', value: 0, annual: true},
			'variablePay': { type: 'int', value: 0, annual: true},
			'hra': { type: 'int', value: 0, annual: true},
			'lta': { type: 'int', value: 0, annual: true},
			'conveyance': { type: 'int', value: 0, annual: true},
			'medical': { type: 'int', value: 0, annual: true},
			'meal': { type: 'int', value: 0, annual: true},
			'special': { type: 'int', value: 0, annual: true},
			'mobile': { type: 'int', value: 0, annual: true},
			'telephone': { type: 'int', value: 0, annual: true},
			'books': { type: 'int', value: 0, annual: true},
			'car': { type: 'int', value: 0, annual: true},
			'driver': { type: 'int', value: 0, annual: true},
			'employeePF': { type: 'int', value: 0, annual: true},
			'employerPF': { type: 'int', value: 0, annual: true},
			'pt': { type: 'int', value: 0, annual: true},
			'rent': { type: 'int', value: 0, annual: false},
			'ppf': { type: 'int', value: 0, annual: true},
			'investments': { type: 'int', value: 0, annual: true},
			'atc': { type: 'int', value: 0, annual: true},
			'medicalPremium': { type: 'int', value: 0, annual: true},
			'medicalPremiumDependant': { type: 'int', value: 0, annual: true},
			'nps': { type: 'int', value: 0, annual: true},
			'housingLoan': { type: 'int', value: 0, annual: true},
			'electricLoan': { type: 'int', value: 0, annual: true},
			'educationLoan': { type: 'int', value: 0, annual: true},
			'preventiveHealth': { type: 'int', value: 0, annual: true},
			'preventiveHealthDependant': { type: 'int', value: 0, annual: true},
			'medicalPremiumHandicapped': { type: 'int', value: 0, annual: true},
			'medicalPremiumHandicappedSevere': { type: 'int', value: 0, annual: true},
			'medicalTreatment': { type: 'int', value: 0, annual: true},
			'medicalTreatmentSC': { type: 'int', value: 0, annual: true},
			'medicalTreatmentVSC': { type: 'int', value: 0, annual: true},
			'physicalDisabilityA40': { type: 'int', value: 0, annual: true},
			'physicalDisabilityB40': { type: 'int', value: 0, annual: true},
			'equityScheme': { type: 'int', value: 0, annual: true},
			'lta-receipts': { type: 'bool', value: false, annual: true},
			'mobile-receipts': { type: 'bool', value: false, annual: true},
			'telephone-receipts': { type: 'bool', value: false, annual: true},
			'books-receipts': { type: 'bool', value: false, annual: true},
			'car-receipts': { type: 'bool', value: false, annual: true},
			'driver-receipts': { type: 'bool', value: false, annual: true},
			'rent-city': { type: 'bool', value: false, annual: true},
		}
		var data = (localStorage.getItem("data")=="null" ||localStorage.getItem("data")=="" || localStorage.getItem("data")==undefined || localStorage.getItem("data")==null)?this.defaultData:JSON.parse(localStorage.getItem("data"));
		this.state = {
			'slide': 0,
			'details': data,
			'othersOpen': false,
			'otherAllowancesOpen': false
		}
	}

	changeSlide = (i)=>{
		return ()=>{
			this.setState({'slide': i})
		}
	}

	save = (key)=>{
		return (e)=>{
			this.setState({'details': {...this.state.details, [key]: { ...this.state.details[key], ['value']: e.target.value }}})
		}
	}

	reset = ()=>{
		localStorage.getItem("data", "");
		this.setState({'details': this.defaultData, 'slide': 1})
	}

	saveToggle = (key)=>{
		return (e)=>{
			this.setState({'details': {...this.state.details, [key]: { ...this.state.details[key], ['value']: !this.state.details[key]['value'] }}})
		}
	}

	toggleAnnual = (key)=> (val) =>{
		this.setState({'details': {...this.state.details, [key]: { ...this.state.details[key], ['annual']: val }}})
	}

	toggle = (field) => ()=>{
		this.setState({[field]: !this.state[field]})
	}
	
	numberWithCommas(x) {
		x=parseInt(x).toString();
		var lastThree = x.substring(x.length-3);
		var otherNumbers = x.substring(0,x.length-3);
		if(otherNumbers != '')
			lastThree = ',' + lastThree;
		var res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
		return res;
	}

	share = ()=>{
		if(navigator.share){
			navigator.share({
				title: 'Savemo - Tax Calculator',
				url: 'http://bit.ly/savemo_referral'
			})
		}
	}

	componentDidUpdate(){
		if (this.state['slide']==4) localStorage.setItem("data", null)
	}

	reset = ()=>{
		window.location = '/'
	}
	
	render(){
		console.log(this.state)
		localStorage.setItem('data', JSON.stringify(this.state.details))
		var details = {};
		for( var key in this.state.details){
			if(this.state.details[key]['type']=='int'){
				var val = this.state.details[key]['value'];
				details[key] = isBoolean(val)?val:(isNaN(val)?0:parseInt(val));
				details[key] *= ( this.state.details[key]['annual']? 1 : 12 );
			}else if(this.state.details[key]['type']=='bool'){
				details[key] = this.state.details[key]['value'];
			}
		}

		var _80CDeduction=Math.min(
							150000,
							(
								details['ppf']
								+ details['investments']
								+ details['atc']
								+ details['employeePF']
							)
						);
		
		var _80DDeduction = Math.min(details['medicalPremium'], 25000)
							+ Math.min(details['medicalPremiumDependant'], 25000)
							+ Math.min(details['preventiveHealth'], 5000)
							+ Math.min(details['preventiveHealthDependant'], 5000)

		var _OtherDeduction = 	Math.min(details['nps'], 50000)
								+ Math.min(details['housingLoan'], 150000)
								+ Math.min(details['electricLoan'], 50000)
								+ details['educationLoan']
								+ Math.min(details['medicalPremiumHandicapped'], 75000)
								+ Math.min(details['medicalPremiumHandicappedSevere'], 125000)
								+ Math.min(details['medicalTreatment'], 40000)
								+ Math.min(details['medicalTreatmentSC'], 100000)
								+ Math.min(details['medicalTreatmentVSC'], 100000)
								+ Math.min(details['physicalDisabilityA40'], 125000)
								+ Math.min(details['physicalDisabilityB40'], 75000)
								+ Math.min(details['equityScheme'], 25000);
		var hraDeductions = Math.max(
								0,
								Math.min(details['hra'], ( details['rent-city'] ? (0.5*details['salary']) : 0.4*details['salary']), (details['rent']*12)-(0.1*details['salary']))
							);

		var deductions = _80CDeduction + _80DDeduction + _OtherDeduction;

		var oldTaxableIncome= 
			details['salary']
			+ details['variablePay']
			+ details['hra']
			+ ( !details['lta-receipts'] ? details['lta'] : 0 )
			+ details['conveyance']
			+ details['medical']
			+ details['special']
			+ ( !details['mobile-receipts'] ? details['mobile'] : 0 )
			+ ( !details['telephone-receipts'] ? details['telephone'] : 0 )
			+ ( !details['books-receipts'] ? details['books'] : 0 )
			+ ( !details['car-receipts'] ? details['driver'] : Math.max(0, details['driver'] - 21600) )
			+ ( !details['driver-receipts'] ? details['driver'] : Math.max(0, details['driver'] - 21600) )
			- 50000
			- hraDeductions
			- deductions
			- details['pt'];

		var newTaxableIncome=
			details['salary']
			+ details['variablePay']
			+ details['hra']
			+ details['lta']
			+ details['conveyance']
			+ details['medical']
			+ details['special']
			+ details['mobile']
			+ details['telephone']
			+ details['books']
			+ details['driver']
			- details['pt'];
			
		oldTaxableIncome = oldTaxableIncome<=500000?0:oldTaxableIncome
		newTaxableIncome = newTaxableIncome<=500000?0:newTaxableIncome

		var oldTaxDict = {
			'3': (Math.max(0, oldTaxableIncome-1000000)*0.3),
			'2': (Math.max(0, (Math.min(1000000, oldTaxableIncome)-500000)*0.2)),
			'1': (Math.max(0, (Math.min(500000, oldTaxableIncome)-250000)*0.05))
		}
		var oldTax = oldTaxDict['1']+ oldTaxDict['2']+ oldTaxDict['3']
		oldTax *= 1.04
		
		var newTaxDict = {
			'1': (Math.max(0, newTaxableIncome-1500000)*0.3),
			'2': (Math.max(0, (Math.min(1500000, newTaxableIncome)-1250000)*0.25)),
			'3': (Math.max(0, (Math.min(1250000, newTaxableIncome)-1000000)*0.2)),
			'4': (Math.max(0, (Math.min(1000000, newTaxableIncome)-750000)*0.15)),
			'5': (Math.max(0, (Math.min(750000, newTaxableIncome)-500000)*0.1)),
			'6': (Math.max(0, (Math.min(500000, newTaxableIncome)-250000)*0.05)),
		}
		var newTax = newTaxDict['1']+ newTaxDict['2']+ newTaxDict['3'] + newTaxDict['4'] + newTaxDict['5'] + newTaxDict['6']
		newTax *= 1.04
		console.log(oldTaxableIncome, oldTax)
		console.log(newTaxableIncome, newTax)

		var section1 = (
			<div className={styles['section'] + ' ' + styles['active']}>
				<h>Budget left you confused? We know the feeling.</h>
				<p className={styles['init']}>How many tax slabs are there?</p>
				<p className={styles['init']}>Do I claim house rent or not?</p>
				<p className={styles['init']}>Why is everyone talking about regimes all of a sudden?</p>
				<p className={styles['para']}>
					In the next page, you will have to enter values form your offer letter / annual contract / salary slip.<br/><br/>
					Make sure you keep it handy.<br/><br/>
					The whole thing will take you less than two minutes.
				</p>
				<Link to={`${this.props.match.url}/salary`}><a className={styles['button']}>Get Started</a></Link>
				<img className={styles['man']}
					srcset="
					/img/ques-xxxhdpi.png 3x,
					/img/ques-xxhdpi.png 2x,
					/img/ques-xhdpi.png 1x
					"
					src="/img/ques-xhdpi.png 1x"
				></img>
			</div>
		)

		var section2 = (
			<div className={styles['section'] + ' ' + styles['active']}>
				<h>Let’s start with your income</h><br></br>
				<span className={styles['sub']}><b>&bull;</b> You can get these values from your Offer Letter / Annual Contract / Salary Slip.</span><br/>
				<span className={styles['sub']}><b>Note: </b><br />
					<ul>
						<li>If any of these values are not present in your salary slip or offer letter leave them as 0</li>
						<li>We do not store any of this data. We value your privacy!</li>
					</ul>
				</span>
				<form>
					<Input toggleAnnual={this.toggleAnnual('salary')} annual={this.state['details']['salary']['annual']} value={this.state['details']['salary']['value']} save={this.save('salary')} name="Basic Salary"/>
					<Input toggleAnnual={this.toggleAnnual('variablePay')} annual={this.state['details']['variablePay']['annual']} value={this.state['details']['variablePay']['value']} save={this.save('variablePay')} name="Variable Pay"/>
					<Input toggleAnnual={this.toggleAnnual('hra')} annual={this.state['details']['hra']['annual']} value={this.state['details']['hra']['value']} save={this.save('hra')} name="House Rent Allowance (HRA)"/>
					<Input toggleAnnual={this.toggleAnnual('lta')} annual={this.state['details']['lta']['annual']} value={this.state['details']['lta']['value']} save={this.save('lta')} name="Leave Travel Allowance" checkValue={this.state['details']['lta-receipts']['value']} checkSave={this.saveToggle('lta-receipts')} checkText="I submit receipt/bill for this"/>
					<Input toggleAnnual={this.toggleAnnual('conveyance')} annual={this.state['details']['conveyance']['annual']} value={this.state['details']['conveyance']['value']} save={this.save('conveyance')} name="Conveyance / Vehicle Allowance"/>
					<Input toggleAnnual={this.toggleAnnual('medical')} annual={this.state['details']['medical']['annual']} value={this.state['details']['medical']['value']} save={this.save('medical')} name="Medical Reimbursement"/>
					<Input toggleAnnual={this.toggleAnnual('meal')} annual={this.state['details']['meal']['annual']} value={this.state['details']['meal']['value']} save={this.save('meal')} name="Meal Coupons (Sodexo)"/>
					<Input toggleAnnual={this.toggleAnnual('special')} annual={this.state['details']['special']['annual']} value={this.state['details']['special']['value']} save={this.save('special')} name="Special Allowance / Flexi-Pay Benefit"/>
					<Input toggleAnnual={this.toggleAnnual('employeePF')} annual={this.state['details']['employeePF']['annual']} value={this.state['details']['employeePF']['value']} save={this.save('employeePF')} name="Employee Contribution to PF"/>
					<Input toggleAnnual={this.toggleAnnual('employerPF')} annual={this.state['details']['employerPF']['annual']} value={this.state['details']['employerPF']['value']} save={this.save('employerPF')} name="Employer Contribution to PF"/>
					<Input toggleAnnual={this.toggleAnnual('pt')} annual={this.state['details']['pt']['annual']} value={this.state['details']['pt']['value']} save={this.save('pt')} name="Professional Tax"/>


					<div className={styles['others']}>
						<div className={styles['title']} onClick={this.toggle('otherAllowancesOpen')}>
							<h3>Other Allowances</h3>
							<a><i className={"fas " + (this.state['otherAllowancesOpen']?"fa-angle-up":"fa-angle-down")}></i></a>
						</div>
						<div className={styles['othersDrop'] + ' ' + (this.state['otherAllowancesOpen']?styles['open']:'')}>
							<Input toggleAnnual={this.toggleAnnual('mobile')} annual={this.state['details']['mobile']['annual']} value={this.state['details']['mobile']['value']} save={this.save('mobile')} name="Mobile Handset Reimbursement" checkValue={this.state['details']['mobile-receipts']['value']} checkSave={this.saveToggle('mobile-receipts')} checkText="I submit receipt/bill for this"/>
							<Input toggleAnnual={this.toggleAnnual('telephone')} annual={this.state['details']['telephone']['annual']} value={this.state['details']['telephone']['value']} save={this.save('telephone')} name="Telephone & Internet Reimbursement" checkValue={this.state['details']['telephone-receipts']['value']} checkSave={this.saveToggle('telephone-receipts')} checkText="I submit receipt/bill for this"/>
							<Input toggleAnnual={this.toggleAnnual('books')} annual={this.state['details']['books']['annual']} value={this.state['details']['books']['value']} save={this.save('books')} name="Books, Periodicals and Training" checkValue={this.state['details']['books-receipts']['value']} checkSave={this.saveToggle('books-receipts')} checkText="I submit receipt/bill for this"/>
							<Input toggleAnnual={this.toggleAnnual('car')} annual={this.state['details']['car']['annual']} value={this.state['details']['car']['value']} save={this.save('car')} name="Car Maintenance" checkValue={this.state['details']['car-receipts']['value']} checkSave={this.saveToggle('car-receipts')} checkText="I submit receipt/bill for this"/>
							<Input toggleAnnual={this.toggleAnnual('driver')} annual={this.state['details']['driver']['annual']} value={this.state['details']['driver']['value']} save={this.save('driver')} name="Driver Allowance" checkValue={this.state['details']['driver-receipts']['value']} checkSave={this.saveToggle('driver-receipts')} checkText="I submit receipt/bill for this"/>
						</div>
					</div>

				</form>
				<Link to={`${this.props.match.url}/rent`}><a className={styles['button']}>Next Step: Rent</a></Link>
			</div>
		)

		var section3 = (
			<div className={styles['section'] + ' ' + styles['active']}>
				<h>Rent</h>
				<form>
					<Input toggleAnnual={this.toggleAnnual('rent')} annual={this.state['details']['rent']['annual']} value={this.state['details']['rent']['value']} save={this.save('rent')} name="How much do you pay as rent every month?" checkValue={this.state['details']['rent-city']['value']} checkSave={this.saveToggle('rent-city')} checkText="I live in Delhi/Chennai/Kolkata/Mumbai"/>
				</form>
				<Link to={`${this.props.match.url}/deductions`}><a className={styles['button']}>Last Step: Deductions</a></Link>
			</div>)
		
		console.log(styles['section'] + ' ' + (this.state['slide']==3?styles['active']:''));
		var section4 = (
			<div className={styles['section'] + ' ' + styles['active']}>
				<h>Deductions</h><br/>
				<span className={styles['sub']}><b>Note: </b> All values are per annum.</span>
				<form>
					{ /* Mandatory */}
					<Input toggleAnnual={this.toggleAnnual('ppf')} annual={this.state['details']['ppf']['annual']} value={this.state['details']['ppf']['value']} save={this.save('ppf')} name="Contribution To PPF"/>
					<Input toggleAnnual={this.toggleAnnual('investments')} annual={this.state['details']['investments']['annual']} value={this.state['details']['investments']['value']} save={this.save('investments')} name="80C: Investments in ELSS funds"/>
					<Input toggleAnnual={this.toggleAnnual('atc')} annual={this.state['details']['atc']['annual']} value={this.state['details']['atc']['value']} save={this.save('atc')} name="80C: Others"/>
					<Input toggleAnnual={this.toggleAnnual('medicalPremium')} annual={this.state['details']['medicalPremium']['annual']} value={this.state['details']['medicalPremium']['value']} save={this.save('medicalPremium')} name="80D: Medical Insurance Premium"/>
					<Input toggleAnnual={this.toggleAnnual('medicalPremiumDependant')} annual={this.state['details']['medicalPremiumDependant']['annual']} value={this.state['details']['medicalPremiumDependant']['value']} save={this.save('medicalPremiumDependant')} name="80D: Medical Insurance Premium for Dependant Parents"/>
					<Input toggleAnnual={this.toggleAnnual('nps')} annual={this.state['details']['nps']['annual']} value={this.state['details']['nps']['value']} save={this.save('nps')} name="Contribution To NPS"/>
					<Input toggleAnnual={this.toggleAnnual('housingLoan')} annual={this.state['details']['housingLoan']['annual']} value={this.state['details']['housingLoan']['value']} save={this.save('housingLoan')} name="Additional Interest on Housing Loan"/>
					{ /* Others */}

					<div className={styles['others']}>
						<div className={styles['title']} onClick={this.toggle('othersOpen')}>
							<h3>Others</h3>
							<a><i className={"fas " + (this.state['othersOpen']?"fa-angle-up":"fa-angle-down")}></i></a>
						</div>
						<div className={styles['othersDrop'] + ' ' + (this.state['othersOpen']?styles['open']:'')}>
							<Input toggleAnnual={this.toggleAnnual('electricLoan')} annual={this.state['details']['electricLoan']['annual']} value={this.state['details']['electricLoan']['value']} save={this.save('electricLoan')} name="Interest on Electric Vehicle Loan"/>
							<Input toggleAnnual={this.toggleAnnual('educationLoan')} annual={this.state['details']['educationLoan']['annual']} value={this.state['details']['educationLoan']['value']} save={this.save('educationLoan')} name="Interest on Loan of Higher Education"/>
							<Input toggleAnnual={this.toggleAnnual('preventiveHealth')} annual={this.state['details']['preventiveHealth']['annual']} value={this.state['details']['preventiveHealth']['value']} save={this.save('preventiveHealth')} name="80D: Preventive Health Checkup"/>
							<Input toggleAnnual={this.toggleAnnual('preventiveHealthDependant')} annual={this.state['details']['preventiveHealthDependant']['annual']} value={this.state['details']['preventiveHealthDependant']['value']} save={this.save('preventiveHealthDependant')} name="80D: Preventive Health Checkup"/>
							<Input toggleAnnual={this.toggleAnnual('medicalPremiumHandicapped')} annual={this.state['details']['medicalPremiumHandicapped']['annual']} value={this.state['details']['medicalPremiumHandicapped']['value']} save={this.save('medicalPremiumHandicapped')} name="Medical Insurance Of Handicapped Dependant"/>
							<Input toggleAnnual={this.toggleAnnual('medicalPremiumHandicappedSevere')} annual={this.state['details']['medicalPremiumHandicappedSevere']['annual']} value={this.state['details']['medicalPremiumHandicappedSevere']['value']} save={this.save('medicalPremiumHandicappedSevere')} name="Medical Treatment / Insurance of handicapped Dependant (Severe)"/>
							<Input toggleAnnual={this.toggleAnnual('medicalTreatment')} annual={this.state['details']['medicalTreatment']['annual']} value={this.state['details']['medicalTreatment']['value']} save={this.save('medicalTreatment')} name="Medical Treatment ( Specified Disease only )"/>
							<Input toggleAnnual={this.toggleAnnual('medicalTreatmentSC')} annual={this.state['details']['medicalTreatmentSC']['annual']} value={this.state['details']['medicalTreatmentSC']['value']} save={this.save('medicalTreatmentSC')} name="Medical Treatment (Specified Disease only)- Senior Citizen"/>
							<Input toggleAnnual={this.toggleAnnual('medicalTreatmentVSC')} annual={this.state['details']['medicalTreatmentVSC']['annual']} value={this.state['details']['medicalTreatmentVSC']['value']} save={this.save('medicalTreatmentVSC')} name="Medical Treatment (Specified Disease only)- Very Senior Citizen"/>
							<Input toggleAnnual={this.toggleAnnual('physicalDisabilityA40')} annual={this.state['details']['physicalDisabilityA40']['annual']} value={this.state['details']['physicalDisabilityA40']['value']} save={this.save('physicalDisabilityA40')} name="Permanent Physical disability (Above 40%)"/>
							<Input toggleAnnual={this.toggleAnnual('physicalDisabilityB40')} annual={this.state['details']['physicalDisabilityB40']['annual']} value={this.state['details']['physicalDisabilityB40']['value']} save={this.save('physicalDisabilityB40')} name="Permanent Physical disability (Below 40%)"/>
							<Input toggleAnnual={this.toggleAnnual('equityScheme')} annual={this.state['details']['equityScheme']['annual']} value={this.state['details']['equityScheme']['value']} save={this.save('equityScheme')} name="Rajiv Gandhi Equity Scheme"/>
						</div>
					</div>
					
				</form>
				<Link to={`${this.props.match.url}/result`}><a className={styles['button']}>Calculate</a></Link>
			</div>)
		var percent = parseInt(Math.abs(((newTax - oldTax)*100) / oldTax))
		percent = isNaN(percent)?0:percent
		var section5 = (
			<div className={styles['section'] + ' ' + styles['active']}>
				<h>Here's your tax comparison</h><br/>
				{(oldTax!=newTax) && (
					<div className={styles['results']}>
						<div className={styles['result'] + ' ' + (oldTax<newTax?styles['good']:styles['bad'])}>
							<span>Under the <b>old</b> tax regime you will have to pay</span>
							<span className={styles['val']}>&#8377;{this.numberWithCommas(oldTax)}</span>
							<span className={styles['per']}>
								{(newTax!=0) && (<span>That’s <b>{percent}%</b> more than what you have to pay in the new regime.</span>)}
							</span>
							<img src="/img/image_2.svg"></img>
						</div>
						<div className={styles['result'] + ' ' + (oldTax>newTax?styles['good']:styles['bad'])}>
							<span>Under the <b>new</b> tax regime you will have to pay</span>
							<span className={styles['val']}>&#8377;{this.numberWithCommas(newTax)}</span>
							<span className={styles['per']}>
								{(oldTax!=0) && (<span>That’s <b>{percent}%</b> more than what you used to pay in the old regime.</span>)}
							</span>
							<img src="/img/image_2.svg"></img>
						</div>
					</div>
				)}
				{(oldTax==newTax) && (
					<div className={styles['results']}>
						<div className={styles['result'] + ' ' + styles['neutral']}>
							<span>You will pay the same tax in both the regimes</span>
							<span className={styles['val']}>&#8377;{this.numberWithCommas(oldTax)}</span>
						</div>
					</div>
				)}
				{ 	(oldTax>newTax) &&
					(<div><p>We suggest you switch to the new regime.</p>
					<p>Sometimes change is good.</p></div>)
				}
				{ 	(oldTax<newTax) &&
					(<div><p>We suggest you stick to the old regime.</p>
					<p>After all, if it ain't broke, why fix it.</p></div>)
				}
				<a className={styles['button']} onClick={this.reset}>Reset</a>
				<div className={styles['footer']}>
					<div><img src="/img/group.svg"></img></div>
					{
						(navigator.share) &&
						(<div className={styles['share-box']}>Share this app with your friends <a onClick={this.share} href="javascript:void(0)" className={styles['share']}><i class="fa fa-share-alt" aria-hidden="true"></i></a></div>)
					}
				</div>
			</div>
		)

		var section6 = (
			<div className={styles['final']} style={{display: 'block'}}>
				<div className={styles['banner']}></div>
				<h>We are Savemo</h>
				<p>
					We are building a super simple app so that you don't have to worry about your finances.<br/>
					Sign up to get early access to our beta when we launch later this month.
				</p>
				<Subscribe />
			</div>
		)
		console.log(this.props);
		return (
			<div className={styles['tax-calc']}>
				<div className={styles['container']}>
					<div className={styles['topbar']}><a href="https://www.savemo.app/"><img src="/img/savemo.svg"></img></a></div>
					<div className={styles['card']}>
						<Switch>
							<Route path={`${this.props.match.url}/salary`} render={()=>section2} />
							<Route path={`${this.props.match.url}/rent`} render={()=>section3} />
							<Route path={`${this.props.match.url}/deductions`} render={()=>section4} />
							<Route path={`${this.props.match.url}/result`} render={()=>section5} />
							<Route path={`${this.props.match.url}/`} render={()=>section1} />
						</Switch>
					</div>
					<div className={styles['breaker']}></div>
					<Switch>
						<Route path={`${this.props.match.url}/result`}>
							<Card style={{width: "90%"}}>
								<h>We are Savemo</h>
								<p>
									We are building a super simple app so that you don't have to worry about your finances.<br/>
									Sign up to get early access to our beta when we launch later this month.
								</p>
								<Subscribe />
							</Card>
						</Route>
					</Switch>
				</div>
			</div>
		);
	}
}

export default TaxCalc;
