import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import logo from './logo.svg';
import './App.css';

import Index from 'pages/index';
import TaxCalc from 'pages/tax-calc';
import ScrollToTop from 'components/scroll';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <ScrollToTop>
          <Switch>
            <Route path="/tax-calculator" component={TaxCalc} />
            <Route path="/" component={Index} />
          </Switch>
        </ScrollToTop>
      </BrowserRouter>
    </div>
  );
}

export default App;
