import React from 'react';
import styles from './style.module.scss';

class Question extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			open: props.open
		}
		this.open = this.open.bind(this)
	}

	open(){
		console.log("Open");
		this.setState({
			open: !this.state.open
		})
	}

	render(){
		console.log(styles['open']);
		return (
			<div className={styles['question']}>
				<div className={styles['q']} onClick={this.open}>
					<h3>{this.props.question}</h3>
					<a><i className={"fas " + (this.state.open?"fa-angle-up":"fa-angle-down")}></i></a>
				</div>
				<h2 className={this.state.open?styles['open']:''}>{this.props.answer}</h2>
			</div>
		);
	}
}

export default Question;
