import React from 'react';
import styles from './style.module.scss';

class Subscribe extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			email: '',
			status: ''
		}
	}
	validateEmail(mail)
	{
		if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail))
		{
			return (true)
		}
		return (false)
	}

	update = (e)=>{
		this.setState({'email': e.target.value});
	}
	submit = (e)=>{
		e.preventDefault();
		if(this.validateEmail(this.state['email'])){
			fetch('https://api.airtable.com/v0/app0n6LvMdIVQuSmw/Table%201',{
				method: 'post',
				headers: {
					'Authorization': 'Bearer keyvkioeOfS5i5Q8T',
					'content-type': 'application/json'
				},
				body: JSON.stringify({'fields':{'Email-id': this.state['email']},'typecast': true})
			}).then((res)=>{
				if(res.status==200)
					this.setState({'status': 'All done!'})
			})
		}
		else
			this.setState({'status': 'Invalid email address'})
	}
	render(){
		return (
			<div className={styles['get-started']}>
				<form onSubmit={this.submit}>
					<input type="input" placeholder="Enter your email to start" onChange={this.update}></input>
					<input type="submit" value="GET STARTED"></input>
				</form><br/>
				<span>{this.state['status']}</span>
			</div>
		);
	}
}

export default Subscribe;
