import React from 'react';
import styles from './style.module.scss';

class NavBar extends React.Component {
	constructor(props){
		super(props);
		this.state = {'open': false}
		this.toggle = this.toggle.bind(this)
	}
	toggle(){
		this.setState({'open': !this.state['open']})
	}
	render(){
		return (
			<div className={styles['topbar']}>
				<div className={styles['navbar']}>
					<a className={styles['logo']}>
						<img className={styles['logo-img']} src="img/Group 3.svg" alt="logo"/>
						<img className={styles['logo-text']} src="img/savemo.png" alt="logo-text"/>
					</a>
					<div className={styles['menu']}>
						<a onClick={this.toggle}><i class="fas fa-bars"></i></a>
						<div className={this.state['open']?styles['open']:''}>
							<a>Home</a>
							<a>Why us?</a>
							<a>FAQs</a>
							<a>Blog</a>
							<a>Team</a>
							<span className={styles['close']} onClick={this.toggle}><i class="fas fa-times"></i></span>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default NavBar;
