import React from 'react';
import styles from './style.module.scss';

class Input extends React.Component {

	constructor(props){
		super(props);
		this.state = {'val': this.props.value, 'open': false}
	}

	toggle =(e)=>{
		this.setState({'open': !this.state['open']});
	}

	changeDuration = (val) => (e)=>{
		this.props.toggleAnnual(val)
		this.toggle();
	}

	render(){
		return (
			<div className={styles['input']}>
				<label>{this.props.name}</label><br/>
				<input type="input" name={this.props.name} onChange={this.props.save} value={this.props.value}></input>
					<div className={styles['drop'] +' '+ ((this.state.open)?styles['open']:'')}>
						<div onClick={this.toggle} className={styles['top']}>
							<span data-annual={false} className={styles['selected']}>
								{(this.props.annual) && "Annual"}
								{(!this.props.annual) && "Monthly"}
							</span>
							<span className={styles['icon']}><i class="fas fa-sort-down"></i></span>
						</div>
						<div className={styles['options'] + ' ' + (this.state['open'] && styles['open'])}>
							<span onClick={this.changeDuration(false)}>Monthly</span>
							<span onClick={this.changeDuration(true)}>Annual</span>
						</div>
					</div>
				<br/>
				<div onClick={this.props.checkSave}>
					{this.props.checkSave && (<input type="checkbox" checked={this.props.checkValue}></input>)}
					<label className={styles['sub']}>{this.props.checkText}</label>
				</div>
			</div>
		);
	}
}

export default Input;
