import React from 'react';
import styles from './style.module.scss';

import Question from 'components/question';

class Wonder extends React.Component {
	constructor(props){
		super(props)
		this.questions = [
			{
				question: "What is this app?",
				answer: "We understand that handling money is difficult. On one hand, you want to go out for dinner and on the other, you want to save up for that Europe trip. We help you make both possible.",
				open: true
			},
			{
				question: "What happens to my data?",
				answer: "Your data is none of our business. We don't share it with anyone. We use it to give you better insights into your spendings.",
				open: false
			},
			{
				question: "Can I get an instant loan?",
				answer: "Wrong place buddy. We want to make you save better and avoid loans.",
				open: false
			},
			{
				question: "You guys are awesome. Can I buy you a beer?",
				answer: "Sure. Feel free to reach out to us at hangout@savemo.app",
				open: false
			}
		]
	}
	render(){
		var ques = this.questions.map(x=><Question question={x.question} answer={x.answer} open={x.open}/>)
		return (
			<div className={styles['wonder']}>
				<h>In case you're wondering</h>
				{ques}
			</div>
		);
	}
}

export default Wonder;
